import { request, socketTask } from '../../utils/request.js'
import headerBar from '../../components/header-bar.vue'
let chatSocket = null
export default {
	data() {
		return {
			usernameFoucus: false,
			passwordFoucus: false,
			chatContent: '',
			chats: [],
			users: {},
			scrollTop: 1000,
			isFocus: false
		}
	},
	components: {
		headerBar
	},
	onLoad() {
		this.initConnect((socket) => {
			let chat = {
				type: 'getUserInfo',
				groupId: 1
			}
			socket.send({
				data: JSON.stringify(chat)
			})
		})
	},
	methods: {
		onMessage(response) {
			let that = this
			let message = JSON.parse(response.data)
			if (message.code === -1) {
				uni.clearStorageSync()
				uni.showToast({ title: '登录状态失效', icon: 'loading', mask: true })
				let timer = setTimeout(() => {
					clearTimeout(timer)
					uni.reLaunch({ url: '../login/login' })
				}, 1000)
			} else {
				switch(message.data.type) {
					case 'groupChat': 
						that.chats.push(message.data)
						that.$nextTick(() => {
							this.scrollToBottom()
						})
						break;
					case 'getUserInfo': 
						console.log(message.data)
						let tempUsers = []
						break;
				}
			}
		},
		onError(error) {
			this.initConnect()
		},
		closeSocket() {
			this.initConnect()
		},
		focus() {





			this.isFocus = true
			this.scrollToBottom()
		},
		blur() {



			this.isFocus = false
			this.scrollToBottom()
		},
		loginout() {
			request('/loginout', {}, 'POST').then(res => {
				if (res.code === 0) {
					uni.clearStorageSync()
					uni.showToast({ title: '登出成功', icon: 'loading', mask: true })
					let timer = setTimeout(() => {
						clearTimeout(timer)
						uni.reLaunch({ url: '../login/login' })
					}, 1000)
				} else {
					uni.clearStorageSync()
					uni.reLaunch({ url: '../login/login' })
				}
			})
		},
		sendMessage() {
			const that = this
			uni.hideKeyboard()
			if (that.chatContent === '') {
				return
			}
			let chat = {
				type: "groupChat",
				groupId: 1,
				user: this.username,
				content: {
					type: "text",
					default: that.chatContent
				}
			}
			if (chatSocket) {
				chatSocket.send({
					data: JSON.stringify(chat),
					success: () => {
						that.chatContent = ''
					}
				})
			} else {
				that.initConnect((socket) => {
					socket.send({
						data: JSON.stringify(chat),
						success: () => {
							that.chatContent = ''
						}
					})
				})
			}
			
		},
		// 下拉触顶
		scrolltoupper() {
		},
		// 上拉触底
		scrolltolower() {
		},
		// 初始化连接
		initConnect(fn) {
			chatSocket = null;
			socketTask('/user').then(socket => {
				chatSocket = socket
				chatSocket.onMessage(this.onMessage)
				chatSocket.onError(this.onError)
				chatSocket.onClose(this.closeSocket)
				return socket
			}).then(socket => {
				typeof fn === 'function' && fn(socket)
			})
		},
		scrollToBottom() {
			this.scrollTop += 300
		},
		getNode() {
			return new Promise(function(resolve, reject){
				let query = uni.createSelectorQuery().in(this)
				query.select('#scrollView').boundingClientRect(data => {
					try {
						resolve(data)
					} catch(error) {
						reject(error)
					}
				}).exec();
			})
		}
	},
	onHide() {
		this.initConnect()
	},
	computed: {
		isIphoneX() {
			let systemInfo = uni.getSystemInfoSync()
			return systemInfo.platform === 'ios' && systemInfo.screenHeight >= 812
		},
		username() {
			return uni.getStorageSync('username')
		}
	}
}
